.blog-panel {
  position: relative;
  min-height: 400px;
  min-height: 70vh; }
  .blog-panel .right-side-panel {
    background-color: #F8F8F8;
    height: 100%;
    padding: 1rem;
    position: absolute;
    right: 0;
    top: 0;
    width: 320px; }
    .blog-panel .right-side-panel .tag-heading {
      text-transform: uppercase;
      font-size: 0.778rem; }
    .blog-panel .right-side-panel .tag-list {
      list-style: none;
      margin: 0; }
    .blog-panel .right-side-panel .tag-list-item {
      display: inline-block; }
    .blog-panel .right-side-panel .tag-list-item > a {
      background-color: #FFFFFF;
      border: 1px solid #dfdfdf;
      color: #5c5c48;
      display: block;
      font-size: 0.8rem;
      margin: 5px 0;
      padding: 0.25rem 0.4rem;
      transition: all 0.1s ease-out 0s; }
      .blog-panel .right-side-panel .tag-list-item > a:hover {
        color: #0AAB8A;
        text-decoration: underline;
        background-color: rgba(157, 171, 138, 0.1);
        border-color: rgba(6, 138, 111, 0.3); }
  .blog-panel .blog-list {
    list-style: none;
    margin: 0; }
  .blog-panel .blog-list-item {
    margin: 2rem 0; }
  .blog-panel .blog-list-item-meta {
    font-size: 0.778rem; }

